@import "../../../sass/consts";

.store_home_page {
  background-color: $bg1;
  .top_wrapper {
    background-color: $main2;
    padding-top: 5rem;
    position: relative;

    .custom {
      margin: .5rem;
      position: absolute;
      top: 0;
      width: calc(100% - 1rem);
      z-index: 10;
      background-color: white;
      box-shadow: 0 0 .5rem #999;
      border-radius: .5rem;
    }

    .filter_wrapper {
      background-color: white;
      padding-top: 4.5rem;
      height: 9.5rem;
      .filters {

        &.fixed {
          z-index: 10;
          padding-top: 0;
          position: fixed;
          top: 0;
          width: 100%;
        }
      }
    }
  }


}
